@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Righteous&family=Shanti&display=swap');

body {
  margin: 0;
  font-size: 1rem;
  font-family: 'Shanti';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  background-color: #111;
  color: white;
}

.righteous {
  font-family: "Righteous";
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
